/* Layout */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
   
  }
  
  .preview {
    height: 60vh;
    width: 100vw;
    border:3px red;
  }
  
  .footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 16vh;
  }
  
  /* Toggle Switch */
  .switch {
    position: relative;
    display: inline-block;
    width: 175px;
    height: 102px;
  }
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: lightgray;
    transition: .4s;
    border-radius: 47px;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 95px;
    width: 95px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
  }
  
  input:checked+.slider {
    background-color: #2da44e;
  }
  
  input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked+.slider:before {
    transform: translateX(72px);
  }